/****   request.js   ****/
// axios
import axios from 'axios'
import mdui from 'mdui/dist/js/mdui.js'
//1. axios
const service = axios.create({
  baseURL: "http://8.141.59.124:8003",
  // baseURL: "http://localhost:8003",
  //  ms3s
  timeout: 3 * 3000
})
// 2.
service.interceptors.request.use(config => {
  //token,loading
  //    config.data = JSON.stringify(config.data); //,qs

  if (config.data && config.data._isJSON == true) {
    delete config.data._isJSON
    config.data = JSON.stringify(config.data)
    config.headers = {
      'Content-Type': 'application/json' //
    }
  }

  //    //tokencookielocalStoragejs-cookie
  //    const token = getCookie('');//tokentoken,
  if (localStorage.getItem("token") != null)
    config.headers.token = localStorage.getItem("token")
  //    if(token){
  //       config.params = {'token':token} //
  //       config.headers.token= token; //
  //     }
  return config
}, error => {
  Promise.reject(error)
})

// 3.
service.interceptors.response.use(response => {
  //loading

  return response
}, error => {
  /*****  *****/
  if (error && error.response) {
    // 1.
    // 2.
    switch (error.response.status) {
      case 400:
        error.message = ''
        break;
      case 401:
        error.message = ''
        break;
      case 403:
        error.message = ''
        break;
      case 404:
        error.message = ','
        window.location.href = "/NotFound"
        break;
      case 405:
        error.message = ''
        break;
      case 408:
        error.message = ''
        break;
      case 500:
        error.message = ''
        break;
      case 501:
        error.message = ''
        break;
      case 502:
        error.message = ''
        break;
      case 503:
        error.message = ''
        break;
      case 504:
        error.message = ''
        break;
      case 505:
        error.message = 'http'
        break;
      default:
        error.message = `${error.response.status}`
    }
  } else {
    // 
    if (JSON.stringify(error).includes('timeout')) {
      mdui.alert('');
    }
    error.message = ''
  }
  mdui.alert(error.message)
  //   mdui.snackbar({
  //     message: error.message
  //   });
  /*****  *****/
  //
  return Promise.resolve(error.response)
})
//4.
export default service
