import http from '../http.js'

let resquest = "/user/english-word-favorite"

// 
export function getFavoriteListApi(){
    return http.get(`${resquest}/list`)
}

// 
export function addFavoriteApi(collectName){
    return http.post(`${resquest}/add`, {
        collectName: collectName,
        _isJSON: true
    })
    
}

// 
export function changeFavoriteApi(collectName){
    return http.put(`${resquest}/add`, {
        collectName: collectName,
        _isJSON: true
    })
}