/****   externalRequest.js   ****/
// axios
import axios from 'axios'
import mdui from 'mdui/dist/js/mdui.js'
//1. axios
const service = axios.create({
  //  ms3s
  timeout: 3 * 1000
})
// 2.
service.interceptors.request.use(config => {

  return config
}, error => {
  Promise.reject(error)
})

// 3.
service.interceptors.response.use(response => {
  //loading

  return response
}, error => {
  /*****  *****/
  if (error && error.response) {
    // 1.
    // 2.
    switch (error.response.status) {
      case 400:
        error.message = ''
        break;
      case 401:
        error.message = ''
        break;
      case 403:
        error.message = ''
        break;
      case 404:
        error.message = ','
        window.location.href = "/NotFound"
        break;
      case 405:
        error.message = ''
        break;
      case 408:
        error.message = ''
        break;
      case 500:
        error.message = ''
        break;
      case 501:
        error.message = ''
        break;
      case 502:
        error.message = ''
        break;
      case 503:
        error.message = ''
        break;
      case 504:
        error.message = ''
        break;
      case 505:
        error.message = 'http'
        break;
      default:
        error.message = `${error.response.status}`
    }
  } else {
    // 
    if (JSON.stringify(error).includes('timeout')) {
      mdui.alert('');
    }
    error.message = ''
  }
  mdui.alert(error.message)
  //   mdui.snackbar({
  //     message: error.message
  //   });
  /*****  *****/
  //
  return Promise.resolve(error.response)
})
//4.
export default service
